body {
  color: #2f4f4f;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  line-height: 23px;
}

main {
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  gap: 30px 40px;
  padding: 30px 35px 155px;
  display: grid;
}

.person {
  background: #fff;
  border-radius: 10px;
  padding: 10px;
  transition: transform 1s;
}

.person__sport, .person__pay {
  opacity: 0;
  transition: opacity 1s;
}

.person:hover {
  background: #eee;
  transform: scale(1.1);
}

.person:hover .person__sport, .person:hover .person__pay {
  opacity: 1;
}

.person:hover .person__photo img {
  border-radius: 60px;
  transition: border-radius 1s, transform 1s;
}

.person__photo {
  position: relative;
}

.person__photo, .person img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: border-radius 1s, transform 1s;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 25%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  line-height: 18.4px;
  display: flex;
  position: absolute;
  top: 91px;
  right: 10px;
}

.person__sport:after {
  content: ",";
}

.person__pay:before {
  content: "$";
}

.person__pay:after {
  content: "m";
}

@media (width >= 1000px) and (width <= 1500px) {
  main {
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (width >= 1500px) {
  main {
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(4, 1fr);
  }
}
